  width: 1px;
  height: 1px;
  margin: -1px;
  border: 0;
  padding: 0;
  white-space: nowrap;
  clip-path: inset(100%);
  clip: rect(0 0 0 0);
  overflow: hidden; }

.header {
  position: relative;
  background-color: #1b2d37;
  color: #ffffff; }
  .header::before {
    content: "";
    position: absolute;
    top: 0;
    left: 0;
    width: 100%;
    min-height: 881px;
    background: #1b2d37 url("../../img/bg-header-desktop@1x.png") no-repeat center top; }
    @media (min-resolution: 144dpi), (min-resolution: 1.5dppx) {
      .header::before {
        background-image: url("../../img/bg-header-desktop@2x.png");
        background-size: 1920px 881px; } }

.header__wrapper {
  max-width: 1502px;
  margin: 0 auto;
  padding-left: 45px;
  padding-right: 45px;
  position: relative;
  z-index: 10;
  display: flex;
  flex-wrap: wrap;
  justify-content: space-between;
  align-items: center;
  padding-top: 35px;
  padding-bottom: 35px; }

.header__contacts {
  display: flex;
  justify-content: flex-end;
  align-items: center;
  flex-wrap: wrap;
  width: 70%; }

.header__logo img {
  width: 202px;
  height: 52px; }

.header__link {
  position: relative;
  color: #ffffff;
  padding-left: 37px; }
  .header__link:hover, .header__link:focus {
    color: #00b2ff; }
    .header__link:hover::before, .header__link:focus::before {
      opacity: 0.6; }
  .header__link::before {
    content: "";
    position: absolute;
    left: 0; }
  .header__link--email::before {
    top: 4px;
    width: 19px;
    height: 14px;
    background: url("../../img/email.svg") no-repeat; }
  .header__link--phone {
    margin-left: 9%; }
    .header__link--phone::before {
      top: 2px;
      width: 18px;
      height: 18px;
      background: url("../../img/phone.svg") no-repeat; }

.header__button {
  display: inline-block;
  min-width: 180px;
  height: 45px;
  padding: 0 15px;
  font-family: "Roboto Condensed", "Arial", sans-serif;
  font-size: 14px;
  font-weight: 700;
  line-height: 45px;
  color: #ffffff;
  text-transform: uppercase;
  text-align: center;
  vertical-align: middle;
  letter-spacing: 0.05em;
  border: 1px solid #ffffff;
  border-radius: 5px;
  margin-left: 5%; }
  .header__button:hover, .header__button:focus {
    color: #00b2ff;
    border-color: #00b2ff; }

@media (max-width: 1023px) {
  .header::before {
    min-height: 687px;
    background: #1b2d37 url("../../img/bg-header-tablet@1x.png") no-repeat center -4px; } }
  @media (max-width: 1023px) and (min-resolution: 144dpi), (max-width: 1023px) and (min-resolution: 1.5dppx) {
    .header::before {
      background-image: url("../../img/bg-header-tablet@2x.png");
      background-size: 1514px 691px; } }

@media (max-width: 1023px) {
