    padding-left: 25px;
    padding-right: 25px;
    padding-top: 20px;
    padding-bottom: 20px; }
  .header__link {
    font-size: 0;
    padding-left: 25px; }
    .header__link--email::before {
      top: 1px;
      width: 25px;
      height: 19px;
      background-size: 25px 19px; }
    .header__link--phone {
      margin-left: 33px; }
      .header__link--phone::before {
