    background: #1b2d37 url("../../img/bg-header-mobile@1x.png") no-repeat -625px -14px; } }
  @media (max-width: 767px) and (min-resolution: 144dpi), (max-width: 767px) and (min-resolution: 1.5dppx) {
    .header::before {
      background-image: url("../../img/bg-header-mobile@2x.png");
      background-size: 1446px 662px; } }

@media (max-width: 767px) {
  .header__wrapper {
    padding-left: 15px;
